.order .border--dotted{-webkit-box-flex:1;-ms-flex:1;flex:1
}
.order__value{-webkit-box-flex:2;-ms-flex:2;flex:2;margin-left:11px;text-align:right;color:#999
}
.order__price{font-weight:400;font-size:16px;line-height:16px;margin-bottom:47px
}
.order__price span{font-weight:500
}
.total{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-align:center;-ms-flex-align:center;align-items:center
}
.total__car-info{font-weight:400;font-size:14px;line-height:16px
}
.total__car-manufacturer{font-size:18px;line-height:21px;margin-bottom:8px
}
.total__car-number{display:inline-block;border:1px solid #999;border-radius:4px;padding:4px 8px 2px;margin-bottom:8px
}
.total__car-fuel,.total__car-date{font-weight:300;margin-bottom:8px
}
.total__car-fuel span,.total__car-date span{font-weight:700
}
.total__car-img{width:50%
}
.total__car-img img{width:256px;height:116px
}
.total__title{font-size:24px;line-height:28px;margin-bottom:16px
}
.confirm-order{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column
}
.confirm-order div{font-weight:400;font-size:24px;line-height:28px;color:#121212;margin-bottom:32px
}
.confirm-order .btn-group{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center
}
.confirm-order .btn-group .btn-crimson{border-radius:4px
}
.confirm-order .btn-group a{margin-right:16px
}
.confirm-order__wrapper{position:fixed;top:0;right:0;bottom:0;left:0;width:100%;height:100%;overflow:auto;background:rgba(255,255,255,0.9)
}
.order-number{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;margin-left:128px;height:32px;color:#121212;line-height:16px
}
